/**
 * 样式处理器 - 用于处理DOM元素的内联样式
 */
class Processor {
  private element: HTMLElement | null = null
  private styles: Record<string, string> = {}

  // CSS前缀
  private static readonly prefixes = ['-moz-', '-o-', '-webkit-', '-ms-']

  /**
   * 绑定DOM元素并解析样式
   */
  dom(element: HTMLElement): this {
    this.element = element
    this.parse()
    return this
  }

  /**
   * 解析元素的style属性
   */
  private parse(): this {
    this.styles = {}
    if (!this.element) return this

    const styleAttr = this.element.getAttribute('style')
    if (styleAttr) {
      const pairs = styleAttr.split(';')
      for (const pair of pairs) {
        if (!pair.trim()) continue

        const [key, value] = pair.split(':').map(s => s.trim())
        if (key && value) {
          this.styles[key] = value
        }
      }
    }
    return this
  }

  /**
   * 移除样式属性
   */
  remove(key: string): this {
    if (key in this.styles) {
      delete this.styles[key]
    }
    return this
  }

  /**
   * 移除DOM属性
   */
  removeDomAttr(attrName: string): this {
    this.element?.removeAttribute(attrName)
    return this
  }

  /**
   * 获取带前缀的样式键名
   */
  private getStyleKeys(key: string): string[] {
    const foundKeys: string[] = []
    for (const prefix of Processor.prefixes) {
      const prefixedKey = `${prefix}${key}`
      if (prefixedKey in this.styles) {
        foundKeys.push(prefixedKey)
        break
      }
    }
    return foundKeys.length ? foundKeys : [key]
  }

  /**
   * 设置样式值（支持回调处理）
   */
  private setStyleValues(key: string, valueCb: (oldValue: string) => string): void {
    const keys = this.getStyleKeys(key)
    if (!keys.length) {
      this.styles[key] = valueCb('')
    } else {
      for (const k of keys) {
        const oldValue = this.styles[k] || ''
        this.styles[k] = valueCb(oldValue)
      }
    }
  }

  /**
   * 合并样式
   */
  merge(key: string, value: string | number): this {
    if (key === 'rotatez') {
      // 处理旋转变换
      const transform = `rotatez(${value}deg)`
      this.setStyleValues('transform', (v) => {
        if (/rotatez\(.*?\)/.test(v)) {
          return v.replace(/rotatez\(.*?\)/, transform)
        } else {
          return v ? `${v} ${transform}` : transform
        }
      })
    } else {
      // 普通样式
      this.styles[key] = String(value)
      
      // 处理带前缀的样式
      for (const prefix of Processor.prefixes) {
        const prefixedKey = `${prefix}${key}`
        if (prefixedKey in this.styles) {
          this.styles[prefixedKey] = String(value)
        }
      }
    }
    return this
  }

  /**
   * 获取样式值
   */
  get(key: string, defaultValue: any = null): any {
    if (!this.element) return defaultValue

    switch (key) {
      case 'opacity': {
        let value = this.element.style.opacity
        if (!value) {
          // 尝试带前缀的版本
          for (const prefix of Processor.prefixes) {
            const prefixedKey = `${prefix}${key}`
            if (prefixedKey in this.styles) {
              value = this.styles[prefixedKey]
              break
            }
          }
        }
        return value ? parseFloat(value) : defaultValue
      }

      case 'rotatez': {
        const transform = this.element.style.transform || ''
        let rotateZ = 0

        if (transform.toLowerCase().indexOf('rotatez(') >= 0) {
          const match = transform.toLowerCase()
            .replace(/rotatex\s*\(\s*[0-9\-]*\s*deg\s*\)/g, '')
            .replace(/rotatey\s*\(\s*[0-9\-]*\s*deg\s*\)/g, '')
            .match(/rotatez\s*\(\s*([0-9\-]+)\s*deg\s*\)/)
          
          if (match) {
            rotateZ = parseInt(match[1])
            // 标准化角度到 -180~180
            if (rotateZ > 180) {
              rotateZ -= 360
            } else if (rotateZ < -180) {
              rotateZ += 360
            }
          }
        }
        return rotateZ
      }

      default:
        return this.styles[key] || defaultValue
    }
  }

  /**
   * 将样式应用到DOM元素
   */
  toDom(): this {
    if (!this.element) return this

    const styleArray: string[] = []
    for (const key in this.styles) {
      styleArray.push(`${key}:${this.styles[key]}`)
    }

    if (styleArray.length) {
      this.element.setAttribute('style', styleArray.join(';'))
    } else {
      this.element.removeAttribute('style')
    }
    return this
  }
}

/**
 * 样式处理器工厂
 */
export const StyleProcessor = {
  /**
   * 从DOM元素创建处理器
   */
  fromDom(element: HTMLElement): Processor {
    const processor = new Processor()
    return processor.dom(element)
  }
}
