import { ref, watch } from 'vue'
import { StyleProcessor } from '../utils/style-processor'
import type { SectionData } from '../types'

/**
 * Section样式管理 Composable
 */
export function useSection() {
  const currentSection = ref<HTMLElement | null>(null)
  const currentElement = ref<HTMLElement | null>(null)

  const sectionData = ref<SectionData>({
    id: '',
    width: 100,
    rotate: 0,
    opacity: 1,
  })

  const imageData = ref({
    show: false,
    element: null as HTMLElement | null,
    width: 100,
    opacity: 1,
  })

  /**
   * 准备Section样式数据
   */
  const prepareSectionStyle = (section: HTMLElement) => {
    currentSection.value = section

    // 获取样式ID
    sectionData.value.id = section.getAttribute('data-section-id') || ''

    // 解析样式
    const processor = StyleProcessor.fromDom(section)

    // 宽度
    const widthStr = section.style.width || '100%'
    sectionData.value.width = parseInt(widthStr) || 100

    // 旋转
    sectionData.value.rotate = processor.get('rotatez', 0)

    // 透明度
    sectionData.value.opacity = processor.get('opacity', 1)
  }

  /**
   * 准备图片样式数据
   */
  const prepareImageStyle = (section: HTMLElement, img: HTMLElement) => {
    imageData.value.element = img
    imageData.value.show = true

    currentSection.value = section

    const processor = StyleProcessor.fromDom(img)

    // 宽度
    const widthStr = img.style.width || '100%'
    imageData.value.width = parseInt(widthStr) || 100

    // 透明度
    imageData.value.opacity = processor.get('opacity', 1)
  }

  /**
   * 清除选中状态
   */
  const clearSelection = () => {
    currentSection.value = null
    currentElement.value = null
    imageData.value.show = false
    imageData.value.element = null
  }

  // 监听Section宽度变化
  watch(() => sectionData.value.width, (newWidth) => {
    if (currentSection.value) {
      StyleProcessor.fromDom(currentSection.value)
        .merge('margin', '0 auto')
        .merge('width', `${newWidth}%`)
        .toDom()
        .remove('max-width')
        .removeDomAttr('width')
    }
  })

  // 监听Section旋转变化
  watch(() => sectionData.value.rotate, (newRotate) => {
    if (currentSection.value) {
      StyleProcessor.fromDom(currentSection.value)
        .merge('rotatez', newRotate)
        .toDom()
    }
  })

  // 监听Section透明度变化
  watch(() => sectionData.value.opacity, (newOpacity) => {
    if (currentSection.value) {
      StyleProcessor.fromDom(currentSection.value)
        .merge('opacity', newOpacity)
        .toDom()
    }
  })

  // 监听图片宽度变化
  watch(() => imageData.value.width, (newWidth) => {
    if (imageData.value.element) {
      StyleProcessor.fromDom(imageData.value.element)
        .remove('max-width')
        .merge('width', `${newWidth}%`)
        .toDom()
        .removeDomAttr('width')
    }
  })

  // 监听图片透明度变化
  watch(() => imageData.value.opacity, (newOpacity) => {
    if (imageData.value.element) {
      StyleProcessor.fromDom(imageData.value.element)
        .merge('opacity', newOpacity)
        .toDom()
    }
  })

  return {
    currentSection,
    currentElement,
    sectionData,
    imageData,
    prepareSectionStyle,
    prepareImageStyle,
    clearSelection
  }
}
