/**
 * UEditor动态加载器
 */

// 声明全局UEditor
declare global {
  interface Window {
    UE?: any
    UEDITOR_HOME_URL?: string
  }
}

let isLoading = false
let isLoaded = false
const loadCallbacks: Array<() => void> = []

/**
 * 加载UEditor脚本
 */
export function loadUEditor(ueditorPath: string = '/ueditor-plus'): Promise<any> {

  return new Promise((resolve, reject) => {
    // 已加载
    if (isLoaded && window.UE) {
      resolve(window.UE)
      return
    }

    // 正在加载中，添加到回调队列
    if (isLoading) {
      loadCallbacks.push(() => resolve(window.UE))
      return
    }

    isLoading = true

    // 设置UEditor基础路径
    const basePath = ueditorPath.endsWith('/') ? ueditorPath : `${ueditorPath}/`
    window.UEDITOR_HOME_URL = basePath

    // 加载配置文件
    const configScript = document.createElement('script')
    configScript.src = `${basePath}ueditor.config.js`
    configScript.onload = () => {
      // 加载主文件
      const mainScript = document.createElement('script')
      mainScript.src = `${basePath}ueditor.all.js`
      mainScript.onload = () => {
        isLoaded = true
        isLoading = false
        
        // 执行所有回调
        loadCallbacks.forEach(cb => cb())
        loadCallbacks.length = 0
        
        resolve(window.UE)
      }
      mainScript.onerror = () => {
        isLoading = false
        reject(new Error('Failed to load UEditor main script'))
      }
      document.head.appendChild(mainScript)
    }
    configScript.onerror = () => {
      isLoading = false
      reject(new Error('Failed to load UEditor config script'))
    }
    document.head.appendChild(configScript)
  })
}

/**
 * 检查UEditor是否已加载
 */
export function isUEditorLoaded(): boolean {
  return isLoaded && !!window.UE
}

/**
 * 获取UEditor实例（如果已加载）
 */
export function getUEditor(): any {
  return window.UE
}
