import {ref, onMounted} from 'vue'
import type {MaterialCategory, MaterialItem, MaterialQueryParams, DesignerConfig} from '../types'
import {Config} from '../utils/config'

/**
 * 素材管理 Composable
 */
export function useMaterial(config: DesignerConfig) {
    const categories = ref<MaterialCategory[]>([])
    const materials = ref<MaterialItem[]>([])
    const total = ref(0)

    const loading = ref(false)
    const loadingMore = ref(false)
    const loadingCategories = ref(false)

    const currentCategory = ref<number | string>(0)
    const keywords = ref('')
    const currentPage = ref(1)
    const pageSize = ref(100)
    const hasMore = ref(true)

    /**
     * 获取分类加载器
     */
    const getCategoryLoader = () => {
        if (config.categoryLoader) {
            return config.categoryLoader
        }

        return Config.DefaultCategoryLoader
    }

    /**
     * 获取素材加载器
     */
    const getStyleLoader = () => {
        if (config.styleLoader) {
            return config.styleLoader
        }

        return Config.DefaultStyleLoader
    }

    /**
     * 加载分类数据（只在初始化时调用一次）
     */
    const loadCategories = async () => {
        const loader = getCategoryLoader()
        if (!loader) return

        loadingCategories.value = true
        try {
            categories.value = await loader()
        } catch (error) {
            console.error('Failed to load categories:', error)
            categories.value = []
        } finally {
            loadingCategories.value = false
        }
    }

    /**
     * 加载素材数据
     */
    const loadMaterials = async (params?: MaterialQueryParams, append = false) => {
        const loader = getStyleLoader()
        if (!loader) return

        if (append) {
            loadingMore.value = true
        } else {
            loading.value = true
        }

        try {
            const data = await loader({
                categoryId: currentCategory.value,
                keywords: keywords.value,
                page: currentPage.value,
                pageSize: pageSize.value,
                ...params
            })

            if (append) {
                // Append mode: add to existing records
                materials.value = [...materials.value, ...data.records]
            } else {
                // Replace mode: replace all records
                materials.value = data.records
            }

            total.value = data.total
            pageSize.value = data.pageSize

            // Check if there are more pages
            const totalPages = Math.ceil(data.total / pageSize.value)
            hasMore.value = currentPage.value < totalPages

        } catch (error) {
            console.error('Failed to load materials:', error)
            if (!append) {
                materials.value = []
                total.value = 0
                hasMore.value = false
            }
        } finally {
            if (append) {
                loadingMore.value = false
            } else {
                loading.value = false
            }
        }
    }

    /**
     * 加载更多素材（下一页）
     */
    const loadMore = async () => {
        if (loadingMore.value || !hasMore.value) return

        currentPage.value++
        await loadMaterials(undefined, true)
    }

    /**
     * 切换分类
     */
    const changeCategory = (categoryId: number | string) => {
        currentCategory.value = categoryId
        currentPage.value = 1
        hasMore.value = true
        loadMaterials()
    }

    /**
     * 搜索
     */
    const search = (keyword: string) => {
        keywords.value = keyword
        currentPage.value = 1
        hasMore.value = true
        loadMaterials()
    }

    // 初始加载
    onMounted(async () => {
        // Load categories first (only once)
        await loadCategories()
        // Then load materials
        await loadMaterials()
    })

    return {
        // Data
        categories,
        materials,
        total,

        // Loading states
        loading,
        loadingMore,
        loadingCategories,

        // Filter states
        currentCategory,
        keywords,
        currentPage,
        hasMore,

        // Methods
        loadMaterials,
        loadMore,
        changeCategory,
        search
    }
}
