<template>
  <div class="upd-material-panel">
    <!-- 素材内容 -->
    <div class="upd-material-body">
      <div class="upd-material-item">
        <!-- 头部：分类和过滤 -->
        <div class="upd-material-item__head">
          <!-- 分类 -->
          <div class="upd-category">
            <div class="upd-category__item">
              <div
                  class="upd-category__title"
                  :class="{ 'upd-category__title--active': currentCategory === 0 }"
                  @click="$emit('change-category', 0)"
              >
                全部
              </div>
            </div>
            <div
                v-for="cat in categories"
                :key="cat.id"
                class="upd-category__item"
            >
              <div
                  class="upd-category__title"
                  :class="{
                  'upd-category__title--active':
                    cat.id === currentCategory ||
                    (cat._child && cat._child.some(c => c.id === currentCategory))
                }"
                  @click="$emit('change-category', cat.id)"
              >
                {{ cat.title }}
              </div>
              <!-- 子分类 -->
              <div v-if="cat._child && cat._child.length" class="upd-category__children">
                <div
                    v-for="child in cat._child"
                    :key="child.id"
                    class="upd-category__child-title"
                    :class="{ 'upd-category__child-title--active': child.id === currentCategory }"
                    @click.stop="$emit('change-category', child.id)"
                >
                  {{ child.title }}
                </div>
              </div>
            </div>
          </div>

          <!-- 过滤和搜索 -->
          <div class="upd-filter">
            <div class="upd-filter__text">
              <!-- 可以添加过滤选项 -->
            </div>
            <div class="upd-filter__search">
              <input
                  v-model="searchKeyword"
                  type="text"
                  placeholder="样式ID或关键词"
                  @keyup.enter="handleSearch"
              />
            </div>
          </div>
        </div>

        <!-- 素材列表 -->
        <div class="upd-material-item__body">
          <div v-if="loading" class="upd-loading">
            加载中...
          </div>
          <div v-else class="upd-material-list">
            <div
                v-for="item in materials"
                :key="item.id"
                class="upd-material-list__item"
                @click="$emit('select', item)"
            >
              <div class="upd-material-list__preview" v-html="item.html"></div>
              <div class="upd-material-list__info">
                <span class="upd-material-list__title">{{ item.title }}</span>
                <span class="upd-material-list__id">ID: {{ item.id }}</span>
              </div>
            </div>

            <!-- Loading more indicator -->
            <div v-if="loadingMore" class="upd-loading-more">
              加载更多...
            </div>

            <!-- No more data indicator -->
            <div v-else-if="!hasMore && materials.length > 0" class="upd-no-more">
              没有更多数据了
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import {ref, onMounted, onUnmounted} from 'vue'
import type {MaterialCategory, MaterialItem} from '../types'

interface Props {
  categories: MaterialCategory[]
  materials: MaterialItem[]
  total: number
  currentCategory: number | string
  loading?: boolean
  loadingMore?: boolean
  hasMore?: boolean
}

const props = withDefaults(defineProps<Props>(), {
  loading: false,
  loadingMore: false,
  hasMore: true
})

const emit = defineEmits<{
  select: [item: MaterialItem]
  'change-category': [categoryId: number | string]
  search: [keyword: string]
  'load-more': []
}>()

const searchKeyword = ref('')
const scrollContainer = ref<HTMLElement | null>(null)

const handleSearch = () => {
  emit('search', searchKeyword.value)
}

/**
 * Handle scroll event to trigger load more
 */
const handleScroll = () => {
  if (!scrollContainer.value || props.loading || props.loadingMore || !props.hasMore) {
    return
  }

  const container = scrollContainer.value
  const scrollTop = container.scrollTop
  const scrollHeight = container.scrollHeight
  const clientHeight = container.clientHeight

  // Trigger load more when scrolled to bottom (with 50px threshold)
  const threshold = 50
  if (scrollTop + clientHeight >= scrollHeight - threshold) {
    emit('load-more')
  }
}

onMounted(() => {
  scrollContainer.value = document.querySelector('.upd-material-item')
  if (scrollContainer.value) {
    scrollContainer.value.addEventListener('scroll', handleScroll)
  }
})

onUnmounted(() => {
  if (scrollContainer.value) {
    scrollContainer.value.removeEventListener('scroll', handleScroll)
  }
})
</script>

