import type {
    DesignerConfig,
    DesignerInstance,
    MaterialCategory,
    StyleListData,
    MaterialQueryParams,
    UEditorConfig
} from '../types'
import {Config, DefaultUEditorPlusToolbars} from '../utils/config'
import {loadUEditor} from '../utils/ueditor-loader'
import {DefaultIframeStyles} from '../utils/iframe-styles'

/**
 * Internal config type with all properties required
 */
interface InternalConfig {
    container: HTMLElement | string
    ueditorPath: string
    ueditorConfig: UEditorConfig
    categoryLoader: () => Promise<MaterialCategory[]>
    styleLoader: (params?: MaterialQueryParams) => Promise<StyleListData>
    onReady: () => void
    onChange: (content: string) => void
}

/**
 * 编辑器核心类 - 框架无关的纯JS实现
 */
export class DesignerCore implements DesignerInstance {
    private config: InternalConfig
    private container: HTMLElement | null = null
    private editor: any = null
    private editorId: string
    private eventHandlers: Map<string, Function[]> = new Map()
    private resizeHandler: (() => void) | null = null
    private editorElement: HTMLElement | null = null

    constructor(config: DesignerConfig) {
        // Initialize ueditorConfig if not provided
        const ueditorConfig = config.ueditorConfig || {}
        if (!ueditorConfig.iframeCssStylesAddition) {
            ueditorConfig.iframeCssStylesAddition = []
        }
        ueditorConfig.iframeCssStylesAddition = [
            DefaultIframeStyles,
            ...ueditorConfig.iframeCssStylesAddition
        ]
        if (!ueditorConfig.toolbars) {
            ueditorConfig.toolbars = DefaultUEditorPlusToolbars
        }
        ueditorConfig.toolbars[ueditorConfig.toolbars.length - 1].push('liveeditor')

        // Get loaders with backward compatibility
        let categoryLoader = config.categoryLoader || Config.DefaultCategoryLoader
        let styleLoader = config.styleLoader || Config.DefaultStyleLoader

        // 合并默认配置
        this.config = {
            container: config.container || '#editor',
            ueditorPath: config.ueditorPath || '/ueditor-plus',
            ueditorConfig,
            categoryLoader,
            styleLoader,
            onReady: config.onReady || (() => {
            }),
            onChange: config.onChange || (() => {
            }),
        }

        // 生成唯一ID
        this.editorId = `ueditorPlusDesigner_${Date.now()}`
    }

    /**
     * 初始化编辑器
     */
    async init(): Promise<void> {
        // 获取容器
        if (typeof this.config.container === 'string') {
            this.container = document.querySelector(this.config.container)
        } else {
            this.container = this.config.container as HTMLElement
        }

        if (!this.container) {
            throw new Error('Container not found')
        }
        // 加载UEditor
        await loadUEditor(this.config.ueditorPath)
        const UE = (window as any).UE

        if (!UE) {
            throw new Error('UEditor failed to load')
        }

        // 创建编辑器容器
        const editorContainer = document.createElement('div')
        editorContainer.id = this.editorId
        this.container.appendChild(editorContainer)

        // 保存编辑器元素引用
        this.editorElement = editorContainer

        // IMPORTANT: 注册UI插件必须在 UE.getEditor() 调用前执行
        this.registerUIPlugin()

        // 配置UEditor
        const editorConfig: any = {
            autoHeightEnabled: false,
            initialFrameWidth: '100%',
            ...this.config.ueditorConfig,
        }

        // 创建编辑器实例
        this.editor = UE.getEditor(this.editorId, editorConfig)

        // 等待编辑器就绪
        this.editor.ready(() => {
            // 设置resize监听
            this.setupResizeListener()
            // 初始布局
            this.doLayout()

            this.config.onReady()
            this.emit('ready')
        })

        // 监听内容变化
        this.editor.addListener('contentChange', () => {
            const content = this.getContent()
            this.config.onChange(content)
            this.emit('change', content)
        })
    }

    /**
     * 注册UI插件 - 必须在 UE.getEditor() 调用前执行
     */
    private registerUIPlugin(): void {
        const UE = (window as any).UE

        // 注册自定义插件
        UE.registerUI('liveeditor', (editor: any, _uiName: string) => {
            // Section点击事件
            editor.on('click', (_t: any, evt: Event) => {
                const el = evt.target as HTMLElement
                const section = el.closest(`.${Config.SectionClass}`) as HTMLElement

                // 不在section中的点击全部忽略
                if (!section) {
                    return
                }

                // 获取编辑器文档对象
                const editorDocument = editor.document

                // 高亮显示Section - 移除其他Section的激活状态
                const allSections = editorDocument.querySelectorAll(`.${Config.SectionClass}`)
                allSections.forEach((s: Element) => {
                    s.classList.remove(Config.SectionActiveClass)
                })
                section.classList.add(Config.SectionActiveClass)

                // 选中文字时不显示工具面板
                if (editor.selection.getText()) {
                    return
                }

                // 检查是否点击了图片
                let clickedImage: HTMLElement | null = null
                const closeNode = editor.selection.getRange().getClosedNode()
                if (closeNode && closeNode.tagName && closeNode.tagName.toLowerCase() === 'img') {
                    clickedImage = closeNode as HTMLElement
                }

                this.emit('section-click', {section, element: el, image: clickedImage})
            })

            // Section悬停事件
            editor.addListener('mouseover mouseout', (t: string, evt: Event) => {
                const el = evt.target as HTMLElement

                // 移除所有子Section的悬停状态
                const allSubSections = editor.document.querySelectorAll(`section:not(.${Config.SectionClass})`)
                allSubSections.forEach((s: Element) => {
                    s.classList.remove(Config.SectionHoverClass)
                })

                // 高亮显示子Section
                if (!el.classList.contains(Config.SectionClass)) {
                    const subSection = el.closest(`section:not(.${Config.SectionClass})`)
                    if (t === 'mouseover' && subSection) {
                        subSection.classList.add(Config.SectionHoverClass)
                    }
                }
            })
        })
    }

    /**
     * 获取内容
     */
    getContent(): string {
        return this.editor?.getContent() || ''
    }

    /**
     * 设置内容
     */
    setContent(content: string): void {
        this.editor?.setContent(content)
    }

    /**
     * 插入Section
     */
    insertSection(html: string, options?: any): void {
        if (!this.editor) return

        const sectionId = options?.id || ''
        const sectionHtml = `<section class="${Config.SectionClass} ${Config.SectionActiveClass}" data-section-id="${sectionId}">${html}</section>`

        this.editor.execCommand('insertHtml', sectionHtml)
    }

    /**
     * 布局调整 - 自适应高度
     */
    public doLayout(): void {

        if (!this.container || !this.editor || !this.editorElement) return

        const containerHeight = this.container.clientHeight

        // 获取工具栏高度
        const toolbarElement = this.editorElement.querySelector('.edui-editor-toolbarbox') as HTMLElement
        const toolbarHeight = toolbarElement ? toolbarElement.offsetHeight : 0

        // 计算可用高度
        const availableHeight = containerHeight - toolbarHeight

        // 设置宽度和高度
        // this.editor.setWidth(containerWidth)
        this.editor.setHeight(Math.max(availableHeight, 100)) // 最小高度100px
    }

    /**
     * 设置resize监听器
     */
    private setupResizeListener(): void {
        // 防抖函数
        let resizeTimer: number | null = null

        this.resizeHandler = () => {
            if (resizeTimer !== null) {
                window.clearTimeout(resizeTimer)
            }
            resizeTimer = window.setTimeout(() => {
                this.doLayout()
            }, 150) // 150ms防抖延迟
        }

        window.addEventListener('resize', this.resizeHandler)
    }

    /**
     * 销毁编辑器
     */
    destroy(): void {
        // 移除resize监听
        if (this.resizeHandler) {
            window.removeEventListener('resize', this.resizeHandler)
            this.resizeHandler = null
        }

        this.editor?.destroy()
        this.editor = null
        this.eventHandlers.clear()
    }

    /**
     * 事件监听
     */
    on(event: string, callback: Function): void {
        if (!this.eventHandlers.has(event)) {
            this.eventHandlers.set(event, [])
        }
        this.eventHandlers.get(event)!.push(callback)
    }

    /**
     * 移除事件监听
     */
    off(event: string, callback?: Function): void {
        if (!callback) {
            this.eventHandlers.delete(event)
            return
        }

        const handlers = this.eventHandlers.get(event)
        if (handlers) {
            const index = handlers.indexOf(callback)
            if (index > -1) {
                handlers.splice(index, 1)
            }
        }
    }

    /**
     * 触发事件
     */
    private emit(event: string, ...args: any[]): void {
        const handlers = this.eventHandlers.get(event)
        if (handlers) {
            handlers.forEach(handler => handler(...args))
        }
    }

    /**
     * 获取编辑器实例（UEditor原生实例）
     */
    getEditor(): any {
        return this.editor
    }
}
