<template>
  <div class="ueditor-plus-designer">
    <div class="ueditor-plus-designer__body">
      <!-- 素材面板 -->
      <div v-if="showMaterial" class="ueditor-plus-designer__material">
        <MaterialPanel
            :categories="categories"
            :materials="materials"
            :total="total"
            :current-category="currentCategory"
            :loading="loading"
            :loading-more="loadingMore"
            :has-more="hasMore"
            @select="handleMaterialSelect"
            @change-category="changeCategory"
            @search="handleSearch"
            @load-more="loadMore"
        />
      </div>

      <!-- 编辑器区域 -->
      <div class="ueditor-plus-designer__editor">
        <div class="upd-editor-instance">
          <div :id="editorId" style="height:100%;"></div>
        </div>

        <!-- 右侧工具栏 -->
        <div v-if="isIframe" class="upd-toolbar">
          <a class="upd-toolbar__item" href="javascript:;" @click="handleConfirm">
            <IconCheck :size="16"/>
            完成
          </a>
        </div>
      </div>
    </div>

    <!-- Section工具面板 -->
    <SectionTools
        v-if="showSectionTools"
        :section-data="sectionData"
        :image-data="imageData"
        :position="toolsPosition"
        @action="handleSectionAction"
        @image-action="handleImageAction"
        @close="closeSectionTools"
    />
  </div>
</template>

<script setup lang="ts">
import {ref, computed, onMounted, onUnmounted} from 'vue'
import {DesignerCore} from '../core/DesignerCore'
import {useMaterial} from '../composables/useMaterial'
import {useSection} from '../composables/useSection'
import {useSectionActions} from '../composables/useSectionActions'
import {Config} from '../utils/config'
import MaterialPanel from './MaterialPanel.vue'
import SectionTools from './SectionTools.vue'
import IconCheck from './icons/IconCheck.vue'
import type {DesignerConfig, MaterialItem} from '../types'
import '../styles/index.scss'

interface Props {
  config?: DesignerConfig
  isIframe?: boolean
}

const props = withDefaults(defineProps<Props>(), {
  config: () => ({}),
  isIframe: false
})

const emit = defineEmits<{
  ready: []
  change: [content: string]
  confirm: [content: string]
}>()

// 编辑器核心实例
const designerCore = ref<DesignerCore | null>(null)
const editorId = ref(`upd_${Date.now()}`)
const showMaterial = ref(true)
const showSectionTools = ref(false)
const toolsPosition = ref({left: 0, top: 0})

// 使用素材管理
const {
  categories,
  materials,
  total,
  loading,
  loadingMore,
  currentCategory,
  hasMore,
  changeCategory,
  search: searchMaterials,
  loadMore
} = useMaterial(props.config)

// 使用Section管理
const {
  currentSection,
  sectionData,
  imageData,
  prepareSectionStyle,
  prepareImageStyle,
  clearSelection
} = useSection()

// 使用Section操作
const {
  clearSectionFormat,
  insertSection,
  selectSection,
  deleteSection,
  moveSectionUp,
  moveSectionDown
} = useSectionActions()

/**
 * 初始化编辑器
 */
onMounted(async () => {
  const container = document.getElementById(editorId.value)
  if (!container) return

  designerCore.value = new DesignerCore({
    ...props.config,
    container
  })

  try {
    await designerCore.value.init()

    // 监听编辑器事件
    designerCore.value.on('ready', () => {
      emit('ready')
      handleWindowResize();
    })

    designerCore.value.on('change', (content: string) => {
      emit('change', content)
    })

    // 监听Section点击
    designerCore.value.on('section-click', handleSectionClick)

    // 设置窗口resize监听，更新工具面板位置
    window.addEventListener('resize', handleWindowResize)
  } catch (error) {
    console.error('Failed to initialize designer:', error)
  }
})

/**
 * 清理
 */
onUnmounted(() => {
  window.removeEventListener('resize', handleWindowResize)
  designerCore.value?.destroy()
})

/**
 * 处理素材选择
 */
const handleMaterialSelect = (item: MaterialItem) => {
  if (designerCore.value) {
    designerCore.value.insertSection(item.html, {id: item.id})
  }
}

/**
 * 处理搜索
 */
const handleSearch = (keyword: string) => {
  searchMaterials(keyword)
}

/**
 * 处理Section点击
 */
const handleSectionClick = ({section, element, image}: any) => {
  currentSection.value = section

  // 准备Section样式
  prepareSectionStyle(section)

  // 检查是否点击图片
  if (image) {
    prepareImageStyle(section, image)
  } else {
    imageData.value.show = false
  }

  // 显示工具面板并计算位置
  showSectionTools.value = true
  updateToolsPosition(section)
}

/**
 * 更新工具面板位置
 */
const updateToolsPosition = (section: HTMLElement) => {
  if (!designerCore.value) return

  const editor = designerCore.value.getEditor()
  if (!editor) return

  // 获取编辑器 iframe 和文档
  const editorEl = document.querySelector(`#${editorId.value}`)
  if (!editorEl) return

  const iframe = editorEl.querySelector('.edui-editor-iframeholder > iframe') as HTMLIFrameElement
  if (!iframe) return

  const editorDocument = editor.document

  // 计算Section在视口中的位置
  const viewport = getSectionViewport(section, iframe, editorDocument)

  // 工具面板尺寸（估算）
  const toolsWidth = 320
  const toolsHeight = 350

  // 水平居中
  let toolsLeft = viewport.left + (viewport.width - toolsWidth) / 2 - viewport.relativeLeft
  toolsLeft = Math.max(0, Math.min(toolsLeft, viewport.containerWidth - toolsWidth))

  // 智能决定显示在上方还是下方
  let toolsTop
  if (viewport.spaceTop > viewport.spaceBottom && viewport.spaceTop > toolsHeight) {
    // 显示在上方
    toolsTop = viewport.top - toolsHeight - viewport.relativeTop
    toolsTop = Math.max(0, toolsTop)
  } else {
    // 显示在下方
    toolsTop = viewport.top + viewport.height - viewport.relativeTop
    const maxTop = viewport.top + viewport.height + viewport.spaceBottom - viewport.relativeTop - toolsHeight
    toolsTop = Math.min(toolsTop, maxTop)
  }

  toolsPosition.value = {
    left: toolsLeft,
    top: toolsTop
  }
}

/**
 * 获取Section在视口中的位置信息
 */
const getSectionViewport = (section: HTMLElement, iframe: HTMLIFrameElement, editorDocument: Document) => {
  const containerEl = document.querySelector('.ueditor-plus-designer')
  if (!containerEl) {
    return {
      left: 0,
      top: 0,
      width: 0,
      height: 0,
      containerWidth: 0,
      containerHeight: 0,
      spaceTop: 0,
      spaceBottom: 0,
      spaceLeft: 0,
      spaceRight: 0,
      relativeLeft: 0,
      relativeTop: 0
    }
  }

  const iframeRect = iframe.getBoundingClientRect()
  const sectionRect = section.getBoundingClientRect()
  const containerRect = containerEl.getBoundingClientRect()

  // 计算Section相对于iframe的位置
  const viewport = {
    left: iframeRect.left + sectionRect.left - editorDocument.documentElement.scrollLeft,
    top: iframeRect.top + sectionRect.top - editorDocument.documentElement.scrollTop,
    width: sectionRect.width,
    height: sectionRect.height,
    containerWidth: containerRect.width,
    containerHeight: containerRect.height,
    spaceTop: 0,
    spaceBottom: 0,
    spaceLeft: 0,
    spaceRight: 0,
    relativeLeft: containerRect.left,
    relativeTop: containerRect.top
  }

  // 计算可用空间
  viewport.spaceTop = viewport.top - containerRect.top
  viewport.spaceLeft = viewport.left - containerRect.left
  viewport.spaceBottom = containerRect.top + containerRect.height - (viewport.top + viewport.height)
  viewport.spaceRight = containerRect.left + containerRect.width - (viewport.left + viewport.width)

  return viewport
}

/**
 * 处理窗口resize事件
 */
const handleWindowResize = () => {
  // 如果工具面板正在显示且有当前section，更新工具面板位置
  if (showSectionTools.value && currentSection.value) {
    updateToolsPosition(currentSection.value)
  }
}

/**
 * 关闭Section工具
 */
const closeSectionTools = () => {
  // 移除激活状态
  if (currentSection.value) {
    currentSection.value.classList.remove(Config.SectionActiveClass)
  }

  showSectionTools.value = false
  clearSelection()
}

/**
 * 处理Section操作
 */
const handleSectionAction = (action: string) => {
  if (!designerCore.value || !currentSection.value) return

  const editor = designerCore.value.getEditor()
  if (!editor) return

  switch (action) {
    case 'clearFormat':
      clearSectionFormat(editor, currentSection.value)
      closeSectionTools()
      break
    case 'newSectionBefore':
      insertSection(editor, currentSection.value, 'before')
      closeSectionTools()
      break
    case 'newSectionAfter':
      insertSection(editor, currentSection.value, 'after')
      closeSectionTools()
      break
    case 'select':
      selectSection(editor, currentSection.value)
      closeSectionTools()
      break
    case 'delete':
      deleteSection(editor, currentSection.value)
      closeSectionTools()
      break
    case 'moveUp':
      moveSectionUp(editor, currentSection.value)
      closeSectionTools()
      break
    case 'moveDown':
      moveSectionDown(editor, currentSection.value)
      closeSectionTools()
      break
  }
}

/**
 * 处理图片操作
 */
const handleImageAction = (action: string) => {
  // Image action handling removed
  // Users should implement custom logic if needed
  console.log('Image action:', action)
}

/**
 * 确认（用于iframe模式）
 */
const handleConfirm = () => {
  if (designerCore.value) {
    const content = designerCore.value.getContent()
    emit('confirm', content)
  }
}

/**
 * 获取内容（暴露给父组件）
 */
const getContent = () => {
  return designerCore.value?.getContent() || ''
}

/**
 * 设置内容（暴露给父组件）
 */
const setContent = (content: string) => {
  designerCore.value?.setContent(content)
}

// 暴露方法给父组件
defineExpose({
  getContent,
  setContent
})
</script>
