import { Config } from '../utils/config'

/**
 * Section操作 Composable
 */
export function useSectionActions() {
  /**
   * 执行带撤销/重做的操作
   */
  const executeWithUndo = (editor: any, callback: () => void) => {
    if (!editor) return
    
    editor.undoManger.save()
    callback()
    editor.undoManger.save()
    editor.syncCommandState()
  }
  
  /**
   * 清除Section格式
   */
  const clearSectionFormat = (editor: any, section: HTMLElement) => {
    if (!editor || !section) return
    
    executeWithUndo(editor, () => {
      editor.selection.getRange().selectNode(section).select()
      editor.execCommand('removeformat')
      section.className = `${Config.SectionClass} ${Config.SectionActiveClass}`
    })
  }
  
  /**
   * 在Section前/后插入空行
   */
  const insertSection = (editor: any, section: HTMLElement, position: 'before' | 'after') => {
    if (!editor || !section) return
    
    const newSection = document.createElement('section')
    newSection.className = Config.SectionClass
    newSection.setAttribute('data-section-id', '')
    
    const p = document.createElement('p')
    const br = document.createElement('br')
    p.appendChild(br)
    newSection.appendChild(p)
    
    executeWithUndo(editor, () => {
      if (position === 'before') {
        section.parentNode?.insertBefore(newSection, section)
      } else {
        section.parentNode?.insertBefore(newSection, section.nextSibling)
      }
    })
    
    // 设置光标位置
    editor.selection.getRange().setStart(p, 0).setCursor(false, true)
  }
  
  /**
   * 选中Section
   */
  const selectSection = (editor: any, section: HTMLElement) => {
    if (!editor || !section) return
    
    executeWithUndo(editor, () => {
      editor.selection.getRange().selectNode(section).select()
    })
  }
  
  /**
   * 删除Section
   */
  const deleteSection = (editor: any, section: HTMLElement) => {
    if (!editor || !section) return
    
    executeWithUndo(editor, () => {
      section.remove()
    })
  }
  
  /**
   * 上移Section
   */
  const moveSectionUp = (editor: any, section: HTMLElement) => {
    if (!editor || !section) return
    
    const prevSection = section.previousElementSibling
    if (prevSection) {
      executeWithUndo(editor, () => {
        section.parentNode?.insertBefore(section, prevSection)
      })
    }
  }
  
  /**
   * 下移Section
   */
  const moveSectionDown = (editor: any, section: HTMLElement) => {
    if (!editor || !section) return
    
    const nextSection = section.nextElementSibling
    if (nextSection) {
      executeWithUndo(editor, () => {
        section.parentNode?.insertBefore(section, nextSection.nextSibling)
      })
    }
  }
  
  return {
    clearSectionFormat,
    insertSection,
    selectSection,
    deleteSection,
    moveSectionUp,
    moveSectionDown
  }
}
