import type {MaterialQueryParams, MaterialCategory, MaterialItem, StyleListData} from '../types'

/**
 * API response interfaces for external API
 */
interface CategoryAPIResponse {
    code: number
    msg: string
    data: {
        tree: RawCategory[]
    }
}

interface MaterialAPIResponse {
    code: number
    msg: string
    data: {
        page: number
        pageSize: number
        total: number
        records: RawMaterial[]
    }
}

interface RawCategory {
    id: number
    created_at: string
    updated_at: string
    pid: number
    sort: number | null
    title: string
    _child?: RawCategory[]
}

interface RawMaterial {
    id: number
    title: string
    tags?: string[]
    html: string

    [key: string]: any
}

/**
 * Default API base URL
 */
const API_BASE_URL = 'https://paiban.tecmz.com/api/live_rich_editor'

/**
 * Fetch categories from API
 */
export async function fetchCategories(): Promise<MaterialCategory[]> {
    try {
        const response = await fetch(`${API_BASE_URL}/categories`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: '{}'
        })

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`)
        }

        const result: CategoryAPIResponse = await response.json()

        if (result.code !== 0 || !result.data?.tree) {
            throw new Error(result.msg || 'Failed to fetch categories')
        }

        // Convert raw category data to MaterialCategory format
        return convertCategories(result.data.tree)
    } catch (error) {
        console.error('Failed to fetch categories:', error)
        return []
    }
}

/**
 * Convert raw category data to MaterialCategory format
 */
function convertCategories(raw: RawCategory[]): MaterialCategory[] {
    return raw.map(item => ({
        id: item.id,
        title: item.title,
        pid: item.pid,
        sort: item.sort || 0,
        _child: item._child ? convertCategories(item._child) : undefined
    }))
}

/**
 * Fetch materials from API with pagination and filters
 */
export async function fetchMaterials(params?: MaterialQueryParams): Promise<MaterialAPIResponse['data']> {
    try {
        // Build request body for POST request
        const requestBody: any = {
            paginate: {
                page: params?.page || 1,
                pageSize: params?.pageSize || 100,
                search: {}
            }
        }

        if (params?.categoryId) {
            requestBody.paginate.search.categoryId = params.categoryId
        }

        if (params?.keywords) {
            requestBody.paginate.search.keywords = params.keywords
        }

        const response = await fetch(`${API_BASE_URL}/paginate`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(requestBody)
        })

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`)
        }

        const result: MaterialAPIResponse = await response.json()

        if (result.code !== 0 || !result.data) {
            throw new Error(result.msg || 'Failed to fetch materials')
        }

        return result.data
    } catch (error) {
        console.error('Failed to fetch materials:', error)
        return {
            page: 1,
            pageSize: params?.pageSize || 10,
            total: 0,
            records: []
        }
    }
}

/**
 * Convert raw material data to MaterialItem format
 */
function convertMaterials(raw: RawMaterial[]): MaterialItem[] {
    return raw.map(item => ({
        ...item,
        id: item.id,
        title: item.title,
        html: item.html
    }))
}

/**
 * Default category loader function
 * Fetches only categories
 */
export async function defaultCategoryLoader(): Promise<MaterialCategory[]> {
    try {
        return await fetchCategories()
    } catch (error) {
        console.error('Default category loader error:', error)
        return []
    }
}

/**
 * Default style loader function
 * Fetches materials based on query parameters
 */
export async function defaultStyleLoader(params?: MaterialQueryParams): Promise<StyleListData> {
    try {
        const materialsData = await fetchMaterials(params)

        return {
            records: convertMaterials(materialsData.records),
            total: materialsData.total,
            page: materialsData.page,
            pageSize: materialsData.pageSize
        }
    } catch (error) {
        console.error('Default style loader error:', error)
        return {
            records: [],
            total: 0,
            page: 1,
            pageSize: params?.pageSize || 100
        }
    }
}

